<template>
        <div class="box"> 
<h3>İzinli Güncelle</h3>
          <form @submit.prevent="onUpdateForm">
            
            
            <input
            type="text"
            class="form-control"
            placeholder="İsim"
            v-model="izinli.izinli_isim"
            required
          />

        <div class="form-check form-check-inline">
          <input
            type="radio"
            name="izinli_grup"
            value="A"
            v-model="izinli.izinli_grup"
            checked
            required
          />
          <label for="inlineRadio1">A Grubu</label>
        </div>

        <div class="form-check form-check-inline">
          <input
            type="radio"
            name="izinli_grup"
            value="B"
            v-model="izinli.izinli_grup"
            required
          />
          <label for="inlineRadio2">B Grubu</label>
        </div>

          <input
            type="text"
            class="form-control"
            placeholder="İzin Nevisi"
            v-model="izinli.izinli_nevi"
            required
          />

          <input
            type="time"
            class="form-control"
            v-model="izinli.izinli_cikis_saati"
            required
          />

          <input
            type="time"
            class="form-control"
            v-model="izinli.izinli_gelis_saati"
          />

              
          <button class="btn btn-update">Güncelle</button>
  
      

      </form>


        </div>


</template>

<script>
import { db } from "../firebaseDb";
export default {
  data() {
    return {
      izinli: {},
    };
  },
  created() {
    let dbRef = db.collection("izinliler").doc(this.$route.params.id);
    dbRef
      .get()
      .then((doc) => {
        this.izinli = doc.data();
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    onUpdateForm(event) {
      event.preventDefault();
      db.collection("izinliler")
        .doc(this.$route.params.id)
        .update(this.izinli)
        .then(() => {
          console.log("User successfully updated!");
          this.$router.push("/");
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>